body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  z-index: 100;
  background: white;
  width: 700px;
  opacity: 0.9;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 15px;
  border: 7px #c9dbe9 double;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.WeatherList {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between; }

h2 {
  font-size: 25px;
  color: #3d6787;
  font-weight: normal;
  text-align: left; }

.control {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  box-sizing: border-box; }

#addCity {
  background: #c9dbe9;
  font-size: 18px;
  padding: 8px;
  border-radius: 8px;
  border: 2px #c9dbe9 inset; }

*:focus {
  outline: none !important; }

button {
  background: #c9dbe9;
  color: #3d6787;
  border: 2px #c9dbe9 outset;
  border-radius: 8px;
  margin-left: 10px !important;
  padding: 8px;
  font-size: 18px;
  font-weight: bold; }

button:hover {
  background-color: #d29d57;
  border-color: #d5cbab;
  color: white;
  cursor: pointer; }

button:active {
  border-style: inset; }

.circle {
  background: #3d6787;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  color: white;
  border-style: none; }

@media screen and (max-width: 715px) {
  .App {
    width: 300px; }
  .Weather {
    width: 100%; }
  .control {
    width: 100% !important; }
  .favourites {
    flex-direction: column !important; }
  h2:first-of-type {
    text-align: center; }
  #addCity {
    width: 190px; } }

.Loader {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 1em;
  width: 100%;
  text-align: center;
  margin: 10px; }

#wind {
  margin-right: 10px;
  height: 25px;
  width: 25px;
  border: 3px white outset;
  border-radius: 50%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ". N ." "W windDirection E" ". S ."; }

#wind > div {
  align-self: center;
  justify-self: center;
  font-size: 7px;
  font-family: monospace; }

#N {
  grid-area: N; }

#W {
  grid-area: W; }

#windDirection {
  grid-area: windDirection; }

#E {
  grid-area: E; }

#S {
  grid-area: S; }

#windDirection > div {
  font-size: 8px; }

.Weather {
  color: #3d6787;
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 20px; }

.Weather > .mainInfo,
.Weather > .mainInfo > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; }

.row {
  margin: 3px 0;
  padding: 7px;
  display: flex;
  flex-direction: row;
  background: #707e89;
  color: #ffffff;
  align-items: center;
  justify-content: space-between;
  font-size: 15px; }

.rowKey {
  font-weight: bold; }

.rowVal {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: normal; }

.temp {
  font-size: 50px; }

.current {
  flex-direction: row-reverse !important;
  width: 100%;
  padding-right: 20px; }

.currentName {
  align-self: flex-start; }

.error {
  color: #fc000d;
  flex-direction: row;
  display: flex;
  justify-content: space-between;
  padding: 20px !important;
  align-items: center;
  font-weight: bold;
  width: 50%;
  box-sizing: border-box; }

@media screen and (max-width: 715px) {
  .Weather {
    width: 100%;
    flex-direction: column !important; }
  .Weather > div {
    width: 100% !important;
    padding: 0 !important; }
  .cityMain {
    padding: 0 !important;
    order: 3; }
  .mainInfo:not(.current) {
    flex-wrap: wrap; }
  .name {
    min-width: 60%; }
  .temp {
    font-size: 40px !important;
    white-space: nowrap; }
  .error {
    width: 100%; } }

* {
  margin: 0;
  padding: 0; }

body {
  /*To hide the horizontal scroller appearing during the animation*/
  overflow-x: hidden; }

.clouds {
  padding: 100px 0;
  background: #c9dbe9;
  background: -linear-gradient(top, #c9dbe9 0%, #fff 100%);
  background: -moz-linear-gradient(top, #c9dbe9 0%, #fff 100%);
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100vh; }

.cloud {
  width: 200px;
  height: 60px;
  background: #fff;
  border-radius: 200px;
  -moz-border-radius: 200px;
  -webkit-border-radius: 200px;
  position: relative; }

.cloud:before, .cloud:after {
  content: '';
  position: absolute;
  background: #fff;
  width: 100px;
  height: 80px;
  position: absolute;
  top: -15px;
  left: 10px;
  border-radius: 100px;
  -moz-border-radius: 100px;
  -webkit-border-radius: 100px;
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
  -moz-transform: rotate(30deg); }

.cloud:after {
  width: 120px;
  height: 120px;
  top: -55px;
  left: auto;
  right: 15px; }

.x1 {
  left: -20vw;
  top: 12vh;
  -webkit-transform: scale(0.8);
  transform: scale(0.8);
  opacity: 0.8;
  /*opacity proportional to the size*/
  /*Speed will also be proportional to the size and opacity*/
  /*More the speed. Less the time in 's' = seconds*/
  -webkit-animation: moveclouds 50s linear infinite;
  -moz-animation: moveclouds 50s linear infinite;
